import Vue from 'vue'
import VueRouter from 'vue-router'
import RubricNews from "../views/RubricNews"
import Admin from "../views/Admin/Admin"
import ArticleEditor from "../views/Admin/ArticleEditor"
import AllNews from "../views/AllNews"
import Login from "../views/Admin/Login"

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'AllNews',
        component: AllNews
    },
    {
        path: '/rubric/:name',
        name: 'RubricNews',
        props: true,
        component: RubricNews
    },
    {
        path: '/admin',
        name: 'Admin',
        component: Admin
    },
    {
        path: '/article-editor',
        name: 'ArticleEditor',
        component: ArticleEditor
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
